const app = getApp();
export default {
	data() {
		return {
			old_phone: '',
			phone: '',
			code: '',
			reload: '获取验证码',
			timing: false,
			timer: null,
			count: ''
		};
	},
	onLoad() {
		if(app.reToken()) {
			uni.getStorageSync("access_token")
		}
		this.old_phone = uni.getStorageSync("user_phone");
	},
	methods: {
		toBack() {
			uni.navigateTo({
				url: './account'
			})
		},
		getVerCode() {
			if (this.phone.length != 11) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号'
				});
				return false;
			} else if (!this.timing) {
				this.getCode();
				const TIME_COUNT = 60;
				this.reload = '重新获取(' + TIME_COUNT + 's)'
				this.count = TIME_COUNT;
				this.timing = true;
				this.timer = setInterval(() => {
					if (this.count > 0 && this.count <= TIME_COUNT) {
						this.count--;
						this.reload = '重新获取(' + this.count + 's)'
					} else {
						this.timing = false;
						clearInterval(this.timer);
						this.timer = null;
						this.reload = '重新获取'
					}
				}, 1000)
			}
		},
		//获取验证码
		getCode() {
			var requestCode = uni.request({
				url: getApp().globalData.url + 'api/sms',
				method: 'POST',
				data: {
					phone: this.phone
				},
				success: (res) => {
					console.log(res)
					if (res.statusCode == 500) {
						uni.showToast({
							icon: 'none',
							title: '发送失败，请稍后再试',
						});
						return false;
					} else {
						uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: '发送成功，请注意查收',
						});
					}
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: '请求失败，请稍后再试',
					});
				},
			})
		},
		confirm_phone() {
			uni.request({
				url: getApp().globalData.url + 'api/user/mobile/change',
				method: 'POST',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				data: {
					mobile: this.phone,
					code: this.code
				},
				success: (res) => {
					// console.log(res)
					uni.showToast({
						icon: 'none',
						position: 'bottom',
						title: '手机号修改成功',
					});
					uni.reLaunch({
						url: './account'
					})
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: '请求失败，请稍后再试',
					});
				}
			})
		}
	}
}
